import { Component, OnInit, ViewChild } from '@angular/core';
import { QrScannerComponent } from 'angular2-qrscanner';
import { AuthService } from '../core/auth.service';
import { UtilityService } from '../core/utility.service';

@Component({
  selector: 'app-qr-code',
  templateUrl: './qr-code.page.html',
  styleUrls: ['./qr-code.page.scss'],
})
export class QrCodePage implements OnInit {
  elementType = 'url';
  value = '';
  @ViewChild(QrScannerComponent) qrScannerComponent: QrScannerComponent;
  constructor(
    private utility: UtilityService,
    private authService: AuthService
  ) {
    // this.utility.showLoader();
    var data = JSON.parse(localStorage.getItem('userObj'));
    console.log(data);
    this.value = data.email;
  }

  ngOnInit() {
    // this.login();
  }

  login() {
    let cred = this.utility.getLocal('cred');
    cred = JSON.parse(window.atob(cred));
    let email = cred.split(':')[0];
    let password = cred.split(':')[1];
    this.authService.login({ email, password }).subscribe(
      (res: any) => {
        console.log(res);
        const token = res['token'];
        const userObj = res['data'];
        this.utility.setLocal('token', token);
        // this.utility.hideLoader();

        this.utility.setLocal('userObj', JSON.stringify(userObj));
      },
      (err) => {
        // this.utility.hideLoader();
        console.log(err);
        this.utility.showAlert(err.error.message);
      }
    );
  }

  onclick() {
    this.qrScannerComponent.getMediaDevices().then((devices) => {
      console.log(devices);
      const videoDevices: MediaDeviceInfo[] = [];
      for (const device of devices) {
        if (device.kind.toString() === 'videoinput') {
          videoDevices.push(device);
        }
      }
      if (videoDevices.length > 0) {
        let choosenDev;
        for (const dev of videoDevices) {
          if (dev.label.includes('front')) {
            choosenDev = dev;
            break;
          }
        }
        if (choosenDev) {
          this.qrScannerComponent.chooseCamera.next(choosenDev);
        } else {
          this.qrScannerComponent.chooseCamera.next(videoDevices[0]);
        }
      }
    });

    this.qrScannerComponent.capturedQr.subscribe((result) => {
      this.utility.showAlert('Succusfully Scan');
    });
  }
}
